window.onload = () => {
    let photo = document.querySelector("#photoFix")
    let photoM = document.querySelector("#photoFixM")
    if (screen.width < 600) {
        photo.style.display = "none"
        photoM.style.display = "block"
    }
    else {
        photo.style.display = "block"
        photoM.style.display = "none"
    }
};